              <el-menu-item index="1-21" @click="goHerf('#infoResponse')">响应您的请求</el-menu-item>
            </el-menu-item-group>

            <el-menu-item-group title="七、政策概论">
              <el-menu-item index="1-22" @click="goHerf('#policyInform')">本政策的修订和通知</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group title="八、联系我们">
              <el-menu-item index="1-23" @click="goHerf('#contactUs')">如何联系我们</el-menu-item>
