      </el-aside>
      <el-container class="content">
        <div class="main" :class="{ MobileMain: Mobile }">
          <el-card class="box-card el-card.is-always-shadow">
            <div class="head_tltie">
              <h1>EASYWAY个人信息保护政策</h1>
            </div>
            <div class="head_date">
              <span>发布日期：2021年11月1日 </span>
              <span>生效时间：2021年11月1日</span>
            </div>
            <div class="text_intro">
              <p>
                欢迎您访问FASCO
                EASYWAY差旅平台（以下简称“EASYWAY”，也称“我们”）深知个人信息对您的重要性，因此我们非常重视保护您的隐私和个人信息。我们将按照法律法规要求并通过《EASYWAY个人信息保护政策》（以下简称“本政策”）向您说明我们在您使用EASYWAY平台的产品/服务时如何处理您的个人信息，以及我们为您提供的访问、更新、删除和保护这些信息的方式。
              </p>
              <strong>
                在您使用EASYWAY平台的产品/服务之前，请您务必仔细阅读、充分理解本政策，特别是以字体加粗的条款，您应当重点阅读，在确认充分理解并同意后开始使用EASYWAY平台的产品/服务。如对本政策内容有任何疑问、意见或建议，您可通过本政策第八条提供的联系方式与我们联系。
              </strong>
            </div>
            <div class="text_severed"></div>
            <div class="text_warn">
              <strong>请您特别注意本政策的适用范围： </strong>
              <strong>1、本政策适用于EASYWAY平台提供的所有产品和服务。</strong>
              <strong>
                2、本政策不适用于EASYWAY平台的产品/服务中包含的或链接至第三方提供的信息/服务（包括任何第三方应用、网站等），该等信息/服务由第三方负责运营，须遵循该第三方的个人信息保护政策或类似规定。</strong>
            </div>
            <div class="text_severed"></div>
            <!-- 定义 -->
            <div class="text_definition" id="definition">
              <p>定义</p>
              <p>
                1、EASYWAY平台：客户端（包括APP、微信小程序等），客户端适用的终端设备包括但不限于PC、平板电脑、手机等。
              </p>
              <p>
                2、用户/您：访问或使用EASYWAY平台所提供的产品/服务的自然人，包括注册用户及未注册用户。
              </p>
              <p>
                3、个人信息：以电子或者其他方式记录的与已识别或者可识别的自然人有关的各种信息
              </p>
              <p>
                4、敏感个人信息：一旦泄露、非法提供或滥用可能危害人身和财产安全，极易导致个人名誉、身心健康受到损害或歧视性待遇等的个人信息。本政策中涉及的敏感个人信息包括<strong>生物识别、宗教信仰、特定身份、金融账户、行踪轨迹</strong>
                等信息，以及不满十四周岁<strong>未成年人的个人信息</strong>。
              </p>
              <p>
                5、个人信息删除：在实现日常业务功能所涉及的系统中去除个人信息的行为，使其保持不可被检索、访问的状态。
              </p>
              <p>6、儿童：不满十四周岁的未成年人。</p>
              <p>
                7、未成年人：不满十八周岁的自然人为未成年人。十六周岁以上的未成年人，以自己的劳动收入为主要生活来源的，视为完全民事行为能力人。
              </p>
            </div>
            <div class="text_severed"></div>
            <!-- 第一点 目录 -->
            <div class="text_" id="useInfo">
              <strong>一、我们如何收集和使用您的个人信息</strong>
              <p>
                在您使用我们的产品/服务时，您需要/可以选择授权我们收集和使用个人信息的情形包括：
              </p>
              <p>
                1、为了向您提供我们产品/服务的基本功能，您需要授权我们收集、使用必要信息的情形。如您拒绝提供前述必要信息，您将无法正常使用我们的产品/服务；
              </p>
              <p>
                2、为了向您提供我们产品/服务的附加功能，您可以选择授权我们收集、使用信息的情形。如您拒绝提供前述信息，您将无法正常使用相关附加功能或无法实现我们拟达到的功能效果，但并不影响您正常使用我们产品/服务的基本功能。
              </p>
              <strong>请您注意，因我们向您提供的产品和服务种类众多，我们将遵循“合理、正当、必要”原则收集和使用您的个人信息。除此之外，您理解并同意，基于向您提供更好的产品和服务的目的，可能增加或变更收集和使用个人信息的目的、范围和方式。</strong>
              <!-- 1-1  注册和管理 -->
              <p id="Registration">
                <strong>（一）帮助您注册与管理EASYWAY账户</strong>
              </p>

              <p>1、账户注册</p>
              <p>
                若您想使用EASYWAY平台的产品/服务，您首先需要注册一个EASYWAY账户成为EASYWAY平台的会员。注册时，您至少需提供手机号（用于接收短信验证码）并设置密码用以创建EASYWAY账户。如您拒绝提供手机号，您将无法在EASYWAY平台创建账户并使用EASYWAY平台的产品/服务。
              </p>
              <strong>您理解并同意，您使用EASYWAY账户登录后在EASYWAY平台的一切行为均代表您本人。您的EASYWAY账户操作所产生的电子信息记录均为您行为的有效凭据，并由您本人承担由此产生的全部责任。</strong>
              <p>2、资料维护与账户管理</p>
              <p>
                （1）为维护您的个人资料，提升您的服务体验，您可以选择提供真实姓名、性别、出生日期、地区信息等。
              </p>
              <!-- 1-2 下单和管理 -->
              <p id="AccountOrders">
                <strong>（二）为您提供下单与订单管理功能</strong>
              </p>

              <p>
                当您准备订购EASYWAY平台的产品/服务时，EASYWAY平台系统会自动生成您订购该产品/服务的订单。在下单和订单管理过程中，我们可能会收集如下信息：
              </p>
              <p>（1）您所选择的不同产品/服务所要求提供的信息；</p>
              <p>（2）您的联系人姓名、手机号；</p>
              <p>（3）您在使用EASYWAY平台的产品/服务过程中产生的订单信息。</p>
              <p>
                我们收集这些信息是为了帮助您确定交易、获得通知、帮助您查询和管理订单信息，以及提供客服与售后服务；我们还会使用您的订单信息来判断您的交易是否存在异常，以保护您的交易安全。
              </p>
              <p>
                因差旅产品/服务的特殊性，您所选择的以下产品/服务，需要您进一步提交必要的信息才能完成预订，这些信息您需要自行填写，或者授权我们以其他方式帮助您完成信息录入，我们同时也提请您在具体预订过程中予以关注：
              </p>
              <p>
                （1）当您预订境内外机票时，视不同的服务类型，您可能需要提供不同的信息类型。当您预订境内机票时，您至少需要提供乘客姓名、证件类型、<strong>证件号</strong>、手机号信息。当您预订境外机票时，您至少需要提供乘客姓名（含拼音）、性别、国籍、出生日期、证件类型、<strong>证件号</strong>、证件有效期以及联系人手机号信息，视产品不同还可能需要您提供联系人邮箱信息。<br />
                （2）当您预订境内火车票时，会视不同方式需要您提供不同的信息类别。当您选择“EASYWAY预订”时，您至少需要提供乘客姓名、证件类型、<strong>证件号</strong>、手机号信息、乘客类型；当您选择“12306预订”时，您还需要提供12306账号信息。
              </p>
              <p>
                当您预订境外火车票时，至少需要提供乘客姓名、出生日期、<strong>护照号</strong>、联系人手机号信息。视产品或证件类型不同，可能还需要您提供邮箱、性别、国籍、证件有效期。
              </p>
              <p>
                （3）当您预订酒店、民宿和客栈时，您至少需要提供入住人姓名、手机号信息；视产品不同可能还需要您提供<strong>证件号</strong>、邮箱信息。您也可以选择向我们提供您的出生日期和性别信息以使用个别酒店会员服务。
              </p>
              <p>
                （4）当您预订专车服务（包括境内外接送机、境内接送站、境内外打车、境内外包车）时，视不同的服务类型，您可能需要提供不同的信息类型。当您预订境内打车服务时，您至少需要提供乘车人的手机号信息；当您预订境外打车服务时，您还需要提供乘车人的姓名拼音。当您预订境内接送机、接送站、包车服务时，您至少需要提供乘车人的姓名、手机号信息；当您预订境外包车、接送机服务时，您还需要提供乘车人的英文姓名。您也可以选择向我们提供乘车人常用地址（家、公司等）、乘车人微信号、同行人电话、预订酒店电话、第三方即时通讯账号以便司机与您联系或者更为便捷地提供服务。
              </p>
              <p>
                （5）当您预订签证服务时，您至少需要提供姓名、年龄段（区分成年人与儿童的签证申请费）、客户类型（区分办理签证所需材料）、配送地址以及联系人姓名、手机号、邮箱信息。我们会通过您提供的邮箱向您另行告知办理签证的必要材料（视您选择的使领馆的要求而定），该等材料仅会用于向使领馆申请办理签证之目的。
              </p>
              <p>
                （6）当您的出行人中包含未成年人时，我们需要您作为监护人或在取得监护人的同意下提供出行的<strong>未成年人的姓名、证件类型、证件号</strong>。
              </p>

              <p>
                其他需要您进一步提供必要信息才能完成产品/服务的预订的，具体以预订流程中的展示、提示或授权内容为准，请注意仔细阅读。
              </p>
              <!-- 1-3 -->
              <p id="HandleDispute">
                <strong>（三）为您提供客户服务与处理争议</strong>
              </p>
              <p>
                为保证您的账户安全，我们的客服会使用您的会员资料和订单信息，以验证您的身份。当您主动要求我们提供与您订单相关的客户服务时，我们可能会查询您的相关订单信息以便给予您适当的帮助和处理。如您主动要求客服协助您修改有关信息（如配送地址、联系人或联系电话），您可能还需要提供上述信息外的其他信息以便完成修改。
              </p>
              <p>
                当您与我们联系时，我们可能会保存与您的<strong>通信/通话记录和内容，</strong>或您留下的联系方式及相关信息，以便与您联系或帮助您解决问题，或记录相关问题的处理方案及结果。
              </p>
              <!-- 1-4 -->
              <p id="ImproveService">
                <strong>（四）改进EASYWAY平台的产品/服务</strong>
              </p>
              <p>
                1、我们可能会对收集的信息进行去标识化地研究、统计分析和预测，用于改善EASYWAY平台的内容和布局，为商业决策提供产品或服务支撑，以及改进我们的产品和服务。
              </p>
              <p>
                2、我们可能将您的订单数据用于数据分析，以便让我们能够了解您所在的位置、喜好和人口统计信息，或与其他来源（包括第三方）的数据相匹配，从而开发和改进我们的产品和服务。
              </p>
              <p>
                3、我们可能会通过您提供的联系方式邀请您参与市场调查，并基于您参加市场调查所提供的信息，分析、衡量您对我们的产品、服务和网站的兴趣，从而开发和改进我们的产品和服务。如您选择不参与市场调查并提供有关信息（以具体市场调查展示页面为准），不会影响您使用EASYWAY平台的基本功能。
              </p>
              <!-- 1-5 -->
              <p id="specialVersion">
                <strong>（五）收集和使用您的个人信息的特别说明</strong>
              </p>
              <p>
                1、若您提供的信息中含有其他用户的个人信息，例如，您通过EASYWAY平台为他人订购产品/服务时需要提交实际订购人的个人信息，或您通过EASYWAY平台主动上传、发布或共享的信息中涉及其他用户的个人信息的，在前述情形下，在向EASYWAY平台提供这些个人信息之前，您须确保您已经取得本人的同意，并确保其已知晓并接受本政策。若其中涉及儿童个人信息的，您需要事先取得对应儿童监护人的同意。
              </p>
              <p>2、征得授权同意的例外</p>
              <p>
                您充分知晓，根据法律法规要求，以下情形中我们收集、使用您的个人信息无需征得您的授权同意：
              </p>
              <p>（1）为履行EASYWAY法定职责或者法定义务所必需；</p>
              <p>（2）与国家安全、国防安全有关的；</p>
              <p>（3）与公共安全、公共卫生、重大公共利益有关的；</p>
              <p>（4）与犯罪侦查、起诉、审判和判决执行等有关的；</p>
              <p>（5）为订立、履行您作为一方当事人的合同所必需；</p>
              <p>
                （6）在紧急状况下，出于维护您或其他个人的生命健康和财产安全；
              </p>
              <p>
                （7）所收集的个人信息是个人信息主体自行向社会公众公开的或其他已经合法公开的个人信息；
              </p>
              <p>
                （8）从合法公开披露的信息中收集的您的个人信息的，如合法的新闻报道、政府信息公开等渠道；
              </p>
              <p>
                （9）为公共利益实施新闻报道、舆论监督等行为，在合理的范围内处理个人信息；用于维护所提供的产品或服务的安全稳定运行所必需的，例如发现、处置产品或服务的故障；
              </p>
              <p>
                （10）学术研究机构基于公共利益开展统计或学术研究所必要，且对外提供学术研究或描述的结果时，对结果中所包含的个人信息进行去标识化处理的；
              </p>
              <p>（11）法律法规以及国家标准规定的其他情形。</p>
              <p>
                请知悉，根据适用的法律，若我们对个人信息采取技术措施和其他必要措施进行处理，使得数据接收方无法重新识别特定个人且不能复原，则此类处理后数据的使用无需另行向您通知并征得您的同意。
              </p>
            </div>
            <div class="text_severed"></div>

            <!-- 第二点 -->
            <div class="text_">
              <p>
                <strong>二、我们如何共享、转移、委托处理和公开披露您的个人信息</strong>
              </p>
              <p id="SharingOffering"><strong>（一）共享与提供</strong></p>
              <p>1、处理原则</p>
              <p>
                我们可能会向合作伙伴等第三方共享您的订单信息、账户信息、设备信息以及位置信息，以保障为您提供的服务顺利完成。但我们仅会出于合法、正当、必要、特定、诚信、明确的目的共享您的个人信息，并且只会共享提供服务所必要的个人信息。我们将评估第三方收集您的个人信息的合法性、正当性、必要性，同时，要求第三方在您的授权同意范围内处理您的个人信息，采取必要的信息管理措施与技术手段，防止您的个人信息发生泄露、损毁、丢失、篡改等后果。第三方无权将所共享的您的个人信息用于任何其他用途，如要改变个人信息的处理目的、处理方式的，将重新您的授权同意。
              </p>
              <p>2、单独同意</p>
              <p>
                除非符合征得授权同意的例外情形（见本政策“一、我们如何收集和使用您的个人信息”），否则我们将会按照法律法规及国家标准的要求，针对除前述情形之外的某些场景下的个人信息的共享与提供，采取合理的方式另外征得您的单独同意。但不论是否需要征得您的单独同意，我们均会以合理的方式向您明示告知具体向您提供所涉产品/服务的第三方供应商信息及其处理您个人信息的目的、方式和种类，例如，在您准备订购部分EASYWAY平台的产品/服务时，我们可能会在您下单前通过订单填写页面或订单确认页面向您明示告知上述信息，并在必要时征得您授权EASYWAY向供应商或服务商提供您的个人信息的单独同意。具体的单独同意形式，请以具体产品/服务订单页面为准。
              </p>
              <p>
                3、我们的合作伙伴包括以下类型（包含中国境内和中国境外实体）：
              </p>
              <p>
                （1）供应商：包括但不限于为了满足您预订需求的酒店、航空公司、汽车租赁和活动提供商和代理商，我们将向供应商共享个人基本资料、个人身份信息、联系人信息。这些供应商可能根据需要与您联系，以完成差旅产品或服务。
              </p>
              <p>
                （2）业务合作伙伴：我们可能与合作伙伴一起为您提供产品或者服务，将向合作伙伴共享相关个人信息，用于包括通讯服务、客户服务、市场推广、广告投放、技术服务、实名认证服务、咨询服务。
              </p>

              <p>
                （3）关联公司：我们可能会与我们的关联公司共享您的相关个人信息，使我们能够向您提供与差旅相关的或者其他产品或服务，他们会采取不低于本政策同等严格的保护措施。
                请您注意，基于业务合作伙伴和关联公司防范和检测欺诈的目的，我们可能也会审慎地提供您的必要个人信息，这些个人信息的提供会经过严格的内部安全评估和审批并通过签署相应的协议限定这些信息的使用目的。
              </p>
              <p id="informationTransfer"><strong>（二）信息转移</strong></p>
              <p>
                我们不会将您的个人信息转移给任何公司、组织和个人，但以下情况除外：
              </p>
              <p>（1）事先获得您的明确同意或授权；</p>
              <p>
                （2）根据适用的法律法规、法律程序的要求、强制性的行政或司法要求；
              </p>
              <p>
                （3）在涉及合并、分立、收购、资产转让或类似的交易时，如涉及到个人信息转移，我们会在正式进行信息转移前，向您告知接收信息公司、组织的名称和联系方式，并要求新的持有您个人信息的公司、组织继续受本政策的约束，继续履行原先由我们承担的个人信息处理义务。否则,我们将要求该公司、组织重新向您征求授权同意。
              </p>
              <p id="EntrustProcessing"><strong>（三）委托处理</strong></p>
              <p>
                为了提升信息处理效率，降低信息处理成本或提高信息处理准确性，我们可能会委托有专业技术能力的关联公司或EASYWAY以外的外部服务供应商处理您的个人信息。我们会与受托方签署委托处理协议，并要求受托方按照委托处理协议、本政策以及其他任何相关的保密和安全措施来处理个人信息。委托关系不生效、无效或被撤销或者终止时，我们会要求受托方返还、删除您的个人信息(包括但不限于因委托处理获取到的原始信息或副本、摘要等)，不得保留。未经您的授权同意，我们禁止受托方转委托他人处理您的个人信息。
              </p>
              <p id="PublicDisclosure"><strong>（四）公开披露</strong></p>
              <p>我们仅会在以下情形，公开披露您的个人信息：</p>
              <p>
                （1）根据您的需求，在您单独同意的前提下，根据您认可的披露方式下披露您所指定的个人信息；
              </p>
              <p>
                （2）根据法律、法规的要求、强制性的行政执法或司法要求所必须提供您个人信息的情况下，我们可能会依据所要求的个人信息类型和披露方式披露您的个人信息。在符合法律法规的前提下，当我们收到上述披露信息的请求时，我们会要求必须出具与之相应的法律文件，如传票或调查函。
              </p>
            </div>
            <div class="text_severed"></div>

            <!-- 第三点 -->
            <div class="text_">
              <p><strong>三、我们如何存储您的个人信息</strong></p>
              <p id="StorageLocation"><strong>（一）存储地点</strong></p>
              <p>1、处理原则</p>
              <p>
                我们会按照法律法规规定，将中华人民共和国境内收集的用户个人信息存储于中国境内。
              </p>
              <p>2、跨境传输</p>
              <p>
                如果您的个人信息存储地点从中国境内转移到境外的，我们将严格依照法律的规定执行。
              </p>
              <p>需要将您的个人信息传输至境外的场景包括：</p>
              <p>（1）获得您的明确授权；</p>
              <p>（2）当营销场景涉及到境外服务提供商时；</p>
              <p>
                （3）当您通过EASYWAY平台进行跨境交易（如预订境外酒店，预订境外机票，预订火车票、打车服务时涉及境外供应商提供服务时）等个人主动行为；
              </p>
              <p>（4）法律法规的明确要求。</p>
              <p>
                除非符合征得授权同意的例外情形（见本政策“一、我们如何收集和使用您的个人信息”），否则我们将会按照法律法规及国家标准的要求，针对除前述情形之外的个人信息跨境传输情形，采取合理的方式另外征得您的单独同意。但不论是否需要征得您的单独同意，我们均会以合理的方式向您明示告知所涉产品/服务的境内外服务商信息及其处理您个人信息的目的、方式和种类。
              </p>
              <p id="StorageDeadline"><strong>（二）存储期限</strong></p>
              <p>
                我们仅在本政策所述目的所必需且最短的期间内，或法律法规要求的时限内保留您的信息。例如，当您使用EASYWAY平台的注册功能时，我们需要收集您的手机号，且在您提供后并在您使用该功能期间，我们需要持续为您保留您的手机号，以向您正常提供该功能、保障您的账户和系统安全。在超出上述存储期限后，或在您行使个人信息删除权、注销账户的情况下，我们会对您的个人信息进行删除或匿名化处理。
              </p>
              <p>
                但在下列情况下，我们有可能因需符合法律要求，调整个人信息的存储时间：
              </p>
              <p>
                （1）为遵守适用的法律法规等有关规定（例如：《电子商务法》规定：商品和服务信息、交易信息保存时间自交易完成之日起不少于三年）；
              </p>
              <p>（2）为遵守法院判决、裁定或其他法律程序的规定；</p>
              <p>（3）为遵守相关政府机关或法定授权组织的要求；</p>
              <p>
                （4）为维护社会公共利益，为保护EASYWAY平台的用户、我们或我们的关联公司、其他用户或雇员的人身财产安全或其他合法权益所合理必需的用途。
              </p>
              <p id="StopOperation"><strong>（三）停止运营</strong></p>
              <p>
                如我们停止运营EASYWAY平台产品或服务，我们将及时停止继续收集您个人信息的活动，对所持有的个人信息进行删除或匿名化处理。
              </p>
            </div>
            <div class="text_severed"></div>

            <!-- 第四点 -->
            <div id="informationSafety" class="text_">
              <p><strong>四、我们如何保护您的个人信息安全</strong></p>
              <p>
                1、EASYWAY非常重视信息安全，成立了专门的负责团队，负责对个人信息处理活动以及采取的保护措施进行监督。我们努力为您提供信息保护，采取了合适的管理、技术以及物理安全措施，参照国内外信息安全标准及最佳实践建立了与业务发展相适应的信息安全保障体系，已获得ISO27001信息安全管理体系标准认证。
              </p>
              <p>
                2、我们从数据的生命周期角度出发，在数据收集、存储、显示、处理、使用、销毁等各个环节建立了安全防护措施，根据信息敏感程度的级别采取不同的控制措施，包括但不限于访问控制、加密传输进行加密存储、敏感信息脱敏显示等。
              </p>
              <p>
                3、我们对可能接触到您信息的员工也采取了严格管理，可监控他们的操作情况，对于数据访问、内外部传输使用、脱敏、解密等重要操作建立了审批机制，并与上述员工签署保密协议等。
              </p>
              <p>
                4、尽管有前述的安全措施，但同时也请您理解在网络上不存在“完善的安全措施”。我们会按现有的技术提供相应的安全措施来保护您的信息，提供合理的安全保障，我们将尽力做到使您的信息不被泄露、损毁或丢失。
              </p>
              <p>
                5、您的账户均有安全保护功能，请妥善保管您用于登录EASYWAY平台的设备以及您的账户名及密码信息，切勿将设备提供他人用于登录EASYWAY平台或将密码告知他人，如果您发现自己的个人信息泄露，特别是您的账户名和密码发生泄露，请您立即与我们的客服联系，以便我们采取相应的措施。
              </p>
              <p>
                6、请您及时保存或备份您的文字、图片等其他信息，您需理解并接受，您接入我们的服务所用的系统和通讯网络，有可能因我们可控范围外的因素而出现问题。
              </p>
              <p>
                7、在使用EASYWAY平台服务进行网上交易时，请您妥善保护自己的个人信息（包括但不限于出行人姓名、联络方式或联系地址），仅在必要的情形下向他人提供。请使用复杂密码，协助我们保证您的账户安全。我们将尽力保障您发送给我们的任何信息的安全性。为防止他人未经授权使用您的密码或使用您的计算机、移动设备或SIM卡，如您发现自己的个人信息尤其是您的账户或密码发生泄露，请您立即联络EASYWAY平台客服，以便我们核实后根据您的申请采取相应措施。
              </p>
              <p>
                8、在不幸发生个人信息安全事件后，我们将按照法律法规的要求向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。事件相关情况我们将以邮件、信函、电话、推送通知等方式告知您，难以逐一告知个人信息主体时，我们会采取合理、有效的方式发布公告。同时，我们还将按照监管部门要求，上报个人信息安全事件的处置情况。
              </p>
            </div>
            <div class="text_severed"></div>

            <!-- 第五点 -->
            <div id="minorSafety" class="text_">
              <p><strong>五、我们如何保护未成年人的个人信息</strong></p>
              <p>
                1、EASYWAY非常重视对未成年人个人信息的保护。若您是18周岁以下的未成年人，在使用我们的服务前，应事先取得您法定监护人的同意。我们根据《中华人民共和国未成年人保护法》等国家相关法律法规的要求对未成年人的个人信息及隐私进行保护。
              </p>
              <p>
                2、EASYWAY不会主动直接向未成年人收集其个人信息。对于经监护人同意而收集未成年人个人信息的情况，我们仅在法律法规允许、监护人同意或保护未成年人所必要的情况下使用、共享、转移或披露此类信息。
              </p>
              <p>
                3、作为监护人，您可以在EASYWAY平台上对未成人的相关信息进行查询、更正和删除操作。如果有事实证明未成年人并未取得监护人同意的情况下注册和使用了我们的服务，监护人可以联系EASYWAY平台客服，我们会在确认后尽快删除相关未成年人的个人信息。
              </p>
              <p>
                4、<strong>对于不满14周岁的儿童个人信息，我们还会遵循正当必要、知情同意、目的明确、安全保障、依法利用的原则，按照《儿童个人信息网络保护规定》等法律法规的要求进行收集、存储、使用、转移、披露等处理儿童个人信息。</strong>当您作为监护人为被监护的儿童选择使用EASYWAY平台相关差旅服务时，我们可能需要向您收集被监护的儿童个人信息，用于向您履行相关服务之必要。如果您不提供前述信息，您将无法享受我们提供的相关服务。此外，您在使用评价功能时可能会主动向我们提供儿童个人信息，请您明确知悉并谨慎选择。您作为监护人应当正确履行监护职责，保护儿童个人信息安全。
              </p>
              <p>
                5、如您对儿童个人信息相关事宜有任何意见、建议或投诉，请联系我们，具体联系方式详见本政策“八、如何联系我们”。
              </p>
            </div>
            <div class="text_severed"></div>

            <!-- 第六点 -->
            <div class="text_">
              <p><strong>六、您如何行使您的个人信息权利</strong></p>
              <p>您对您的个人信息享有以下权利：</p>
              <p id="infoChange"><strong>（一）个人信息的查询、更正、补充及管理</strong></p>
              <p>
                在EASYWAY差旅APP端登录后，您可以按照下述步骤自行访问、更正、补充及管理您的个人信息。
              </p>
              <p id="infoDelete"><strong>（二）个人信息的删除</strong></p>
              <p>您可以通过部分路径删除您的部分信息。</p>
              <p>
                同时，在下列情形中，您可以通过本政策第八条的联系方式与我们联系，向我们提出删除个人信息的请求，我们将在十五个工作日内回复您的请求：
              </p>
              <p>（1）如果我们处理个人信息的行为违反法律法规；</p>

              <p>
                （2）如果我们收集、使用您的个人信息，却未征得您的明确同意；
              </p>
              <p>（3）如果我们处理个人信息的行为严重违反了与您的约定；</p>
              <p>（4）如果您不再使用我们的产品或服务，或您主动注销了账户；</p>
              <p>（5）如果我们永久不再为您提供产品或服务。</p>
              <p>
                当您或我们协助您删除相关信息后，因为适用的法律要求或安全技术原因，我们可能无法立即从备份系统中删除相应的信息，我们将安全地存储您的个人信息并将其与任何进一步处理隔离，直至备份更新时可以清除这些信息或实现匿名。
              </p>
              <p id="infoRevocation"><strong>（三）改变您授权同意的范围或撤回授权</strong></p>
              <p>
                您可以通过删除您的个人信息、关闭设备功能等方式改变您授权我们继续收集个人信息的范围或撤回您的授权。您也可以通过注销账户的方式，撤回我们继续收集您个人信息的全部授权。但请注意，每个业务功能需要一些基本的个人信息才能得以完成（见本政策“一、我们如何收集和使用您的个人信息”），当您撤回授权后，我们无法继续为您提供所对应的服务。同时，基于部分产品的特殊性和相关法律法规的要求，如果您使用的服务尚未结束（例如，您出行类产品/服务过程尚未结束，或尚有欠款未结清），在服务结束之前您有可能无法撤回您的授权。
              </p>
              <p>
                对于我们持续使用您的个人信息的数据处理行为，当您执行上述操作后，我们将不再处理相应的个人信息，但您的上述操作不会影响此前基于您的授权已经开展的个人信息处理。
              </p>
              <p id="infoLogout"><strong>（四）注销您的账户</strong></p>
              <p>
                您可通过登录EASYWAY差旅APP自行进行账户注销。您还可以通过本政策第八条的联系方式与我们联系，由我们的客服为您提供注销服务。注销EASYWAY平台账户后，您该账户内的所有信息将被清空，我们将不会再收集、使用或共享与该账户相关的个人信息，但之前的信息我们仍需按照监管要求的时间进行保存，且在该依法保存的时间内有权机关仍有权依法查询。
              </p>
              <p id="infoGain"><strong>（五）获取个人信息副本</strong></p>
              <p>
                如您需要您的个人信息的副本，您可以通过本政策第八条的联系方式与我们联系，在核实您的身份后，我们将向您提供您在我们的服务中的个人信息副本（包括基本资料、身份信息），但法律法规另有规定的或本政策另有约定的除外。
              </p>
              <p id="infoTransfer"><strong>（六）个人信息的转移</strong></p>
              <p>
                如您需要将您的个人信息转移至指定的个人信息处理者，您可以通过本政策第八条的联系方式与我们联系，在核实您的身份后，经审核符合国家网信部门规定条件的，我们将向您提供个人信息转移的途径，但法律法规另有规定的或本政策另有约定的除外。
              </p>
              <p>我们也将采取必要的信息管理措施与技术手段，确保转移过程的安全性，防止您的个人信息发生泄露、损毁、丢失、篡改等后果。</p>
              <p id="infoResponse"><strong>（七）响应您的请求</strong></p>
              <p>
                如果您无法通过上述方式访问、更正、补充或删除您的个人信息，或您需要访问、更正、补充或删除您在使用我们产品/服务时所产生的其他个人信息，或您需要获取您的个人信息副本或者转移您的个人信息，或您认为我们存在任何违反法律法规或与您关于个人信息的收集或使用的约定，您均可以通过本政策第八条的联系方式与我们联系。
              </p>
              <p>
                为了保障安全，我们可能需要您提供书面请求，或提供您的身份证明文件，我们将在收到您反馈并验证您的身份后的十五个工作日内答复您的请求。对于您合理的请求，我们原则上不收取费用，但对多次重复、超出合理限度的请求，我们将视情收取一定成本费用。对于无端重复、需要过多技术手段（例如，需要开发新系统或从根本上改变现行惯例）、给他人合法权益带来风险或者非常不切实际（例如，涉及备份服务器上存放的信息）的请求，我们可能会予以拒绝。
              </p>
              <p>在以下情形中，按照法律法规要求，我们将无法响应您的请求：</p>

              <p>（1）与EASYWAY履行法律法规规定的义务相关的；</p>
              <p>（2）与国家安全、国防安全有关的；</p>
              <p>（3）与公共安全、公共卫生、重大公共利益有关的；</p>
              <p>（4）与犯罪侦查、起诉和审判和执行判决等有关的；</p>
              <p>（5）有充分证据表明您存在主观恶意或滥用权利的；</p>
              <p>
                （6）出于维护您或其他个人的生命、财产等重大合法权益但又很难得到您本人同意的；
              </p>
              <p>
                （7）响应您的请求将导致您或其他个人、组织的合法权益受到严重损害的；
              </p>
              <p>（8）涉及商业秘密的。</p>
            </div>
            <div class="text_severed"></div>

            <!-- 第七点 -->
            <div class="text_">
              <p id="policyInform"><strong>七、本政策的修订和通知</strong></p>
              <p>
                我们会在必要时修改个人信息保护政策，请您理解，我们可能会适时修订本政策，我们将标注本政策最近更新的日期，并经公示后生效。未经您明确同意，我们不会削减您按照本政策所应享有的权利。对于重大变更，我们还会提供更为显著的通知（包括对于某些服务，我们会通过电子邮件发送通知，说明个人信息保护政策的具体变更内容）。请您经常回访本政策，以阅读最新版本。
              </p>
              <p>本政策所指的重大变更包括但不限于：</p>
              <p>1、我们的服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式等；</p>
              <p>2、个人信息共享、转移或公开披露的主要对象发生变化；</p>
              <p>3、您参与个人信息处理方面的权利及其行使方式发生重大变化；</p>
              <p>4、我们负责处理个人信息安全的责任部门、联络方式及投诉渠道发生变化时。</p>
            </div>
            <div class="text_severed"></div>
            <!-- 第八点 -->
            <div class="text_">
              <p id="contactUs"><strong>八、如何联系我们</strong></p>
              <p>1、如您有任何与本政策相关的问题、意见或建议，您可以通过访问<a href="http://www.efasco.com">http://www.efasco.com</a>
                在线客服系统，或发送邮件至我们的客服邮箱fascoobt@efasco.com，或拨打我们的客服电话（021-31088488）等多种方式与我们联系。</p>
              <p>2、我们还设立了个人信息保护专职部门，您可以发送邮件至我们个人信息保护负责人的邮箱fascoobt@efasco.com与其联系。</p>
              <p>
                3、一般情况下，我们将在十五个工作日内回复。如果您对我们的回复不满意，特别是我们的个人信息处理行为损害了您的合法权益，您还可以向网信办、消费者保护协会等监管部门进行投诉，或者您还可以通过向上海市徐汇区人民法院提起诉讼以解决争议。
              </p>
            </div>
